.header__link {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  text-decoration: none;
}

@media (max-width: 618px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
  }
}