.card__like {
  width: 22px;
  height: 19px;
  padding: 0;
  border: none;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  cursor: pointer;
  transition: opacity .2s ease-in-out;
}

.card__like:hover {
  opacity: .5;
}
